<template>
  <div ref="content" class="topHeader">
    <b-row>
      <b-col class="mt-1 cancelCase">
        <b-button @click="close" class="back-btn" variant="primary">
          {{
          $t("CLOSE")
          }}
        </b-button>
      </b-col>
    </b-row>
    <b-row>
      <b-col>
        <b-card no-body class="p-1 mt-1 terms-box mb-2">
          <b-card-body>
            <b-row>
              <b-col class="title-fw title-fs text-center mb-2">
                {{
                $t("LISTOGQANDA")
                }}
              </b-col>
            </b-row>
            <b-row class="mb-5">
              <b-col>
                <div>
                  <b-link
                    @click="scrollTo('elm1')"
                    href="javascript:;"
                    class="card-link link-color pr-2"
                  >
                    <span class="bull-element">&bull;</span>&nbsp;
                    <u class="title-fw">サイトからのメールが届かない</u>
                  </b-link>
                </div>
                <div>
                  <b-link
                    @click="scrollTo('elm2')"
                    href="javascript:;"
                    class="card-link link-color pr-2"
                  >
                    <span class="bull-element">&bull;</span>&nbsp;
                    <u class="title-fw">ID・パスワードはあっているのにログインできない</u>
                  </b-link>
                </div>
                <div>
                  <b-link
                    @click="scrollTo('elm3')"
                    href="javascript:;"
                    class="card-link link-color pr-2 mb-1"
                  >
                    <span class="bull-element">&bull;</span>&nbsp;
                    <u class="title-fw">スマホでログインできない</u>
                  </b-link>
                </div>
                <div>
                  <b-link
                    @click="scrollTo('elm4')"
                    href="javascript:;"
                    class="card-link link-color pr-2 mb-1"
                  >
                    <span class="bull-element">&bull;</span>&nbsp;
                    <u class="title-fw">スマホでログインできない</u>
                  </b-link>
                </div>
              </b-col>
            </b-row>

            <b-row>
              <b-col class="qna-check">
                <div ref="elm1">
                  <div class="mb-2">
                    <span class="bull-element">&bull;</span> &nbsp;
                    <span class="title-fw">サイトからのメールが届かない</span>
                  </div>
                  <div
                    class="mb-4"
                  >プロバイダの設定などにより、迷惑メールフォルダなど、本来受信されるべきフォルダ以外に受信されている場合があります。まずはご利用のメールソフトの全てのフォルダをご確認ください。</div>
                </div>

                <div ref="elm2">
                  <div class="mb-2">
                    <span class="bull-element">&bull;</span> &nbsp;
                    <span class="title-fw">ID・パスワードはあっているのにログインできない</span>
                  </div>
                  <div>以下の注意点を参考に今一度ログインをお試しいただくようお願いします。</div>
                  <div>【ID・パスワードの入力時の注意点】</div>
                  <ul>
                    <li>ID(メールアドレス)とパスワードは半角でご入力ください。</li>
                    <li>パスワードは大文字・小文字を区別しますのでご注意ください。</li>

                    <li>「.(ドット)」と「,(カンマ)」の打ち間違いにご注意ください。</li>

                    <li>文字の最初や最後、文字の間にスペースが入力されていないか、ご確認ください。</li>
                  </ul>
                  <div class="mb-4">※コピー＆ペーストや、入力候補から文字を選択すると、文字の前後にスペースが入る場合がございます。</div>
                </div>

                <div ref="elm3">
                  <div class="mb-2">
                    <span class="bull-element">&bull;</span> &nbsp;
                    <span class="title-fw">スマホでログインできない</span>
                  </div>
                  <div
                    class="mb-4"
                  >ユーザIDとパスワードは、ご利用端末を問わず共通で利用可能です。パスワードがご不明な場合はこちらをご参照ください。機種変更などで登録メールアドレスが受信できず、パスワード再設定メールを受け取れない場合はこちらをご参照ください。</div>
                </div>

                <div ref="elm4">
                  <div class="mb-2">
                    <span class="bull-element">&bull;</span> &nbsp;
                    <span class="title-fw">ID・パスワードはあっているのにログインできない</span>
                  </div>
                  <div>以下の注意点を参考に今一度ログインをお試しいただくようお願いします。</div>
                  <div>【ID・パスワードの入力時の注意点】</div>
                  <ul>
                    <li>ID(メールアドレス)とパスワードは半角でご入力ください。</li>
                    <li>パスワードは大文字・小文字を区別しますのでご注意ください。</li>

                    <li>「.(ドット)」と「,(カンマ)」の打ち間違いにご注意ください。</li>

                    <li>文字の最初や最後、文字の間にスペースが入力されていないか、ご確認ください。</li>
                  </ul>
                  <div class="mb-4">※コピー＆ペーストや、入力候補から文字を選択すると、文字の前後にスペースが入る場合がございます。</div>
                </div>
              </b-col>
            </b-row>
          </b-card-body>
        </b-card>
      </b-col>
    </b-row>
    <b-row>
      <b-col class="mt-1 float-right">
        <b-button ref="goup-btn" @click="goUp" class="goup-btn" variant="primary">
          <i class="fa fa-long-arrow-up" aria-hidden="true"></i>
          <br />
          {{ $t("GOUP") }}
        </b-button>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import { Settings } from "luxon";
Settings.defaultLocale = "ja";

export default {
  components: {},
  data() {
    return {};
  },
  computed: {},
  mounted() {
    this.$refs["goup-btn"].style.display = "none";
  },
  created() {
    window.addEventListener("scroll", this.scrollFunction);
  },
  destroyed() {
    window.removeEventListener("scroll", this.scrollFunction);
  },
  methods: {
    close() {
      window.close();
    },
    /** goup button */
    goUp() {
      document.body.scrollTop = 0; // For Safari
      document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE
    },

    /** on scroll hide/dispaly goup button */
    scrollFunction() {
      if (
        document.body.scrollTop > 20 ||
        document.documentElement.scrollTop > 20
      ) {
        this.$refs["goup-btn"].style.display = "block";
      } else {
        this.$refs["goup-btn"].style.display = "none";
      }
    },

    /** scroll on click of links to same page ref */
    scrollTo(refName) {
      const headerOffset = 55; // header height
      let element = this.$refs[refName];
      var top = element.getBoundingClientRect().top - headerOffset;
      window.scrollTo(0, top);
    }
  }
};
</script>

<style scoped lang="scss">
.card {
  border: 0px;
}
.terms-box {
  margin: 0% 25% 0% 25%;
  min-height: 74vh;
}
.topHeader{
  margin-top: 55px !important;
}
.terms-box .card-body {
  padding-top: 0rem;
}
.inline-qna-links {
  display: inline !important;
}
.goup-btn {
  position: fixed;
  bottom: 4rem;
  right: 15%;
  min-width: 20px;
  font-size: 9px;
  padding: 0.7rem 0.4rem 0.7rem 0.4rem;
}
.pr-2 {
  padding-right: 1.5rem !important;
}
.bull-element {
  font-size: 0.7rem;
}
.scroll-container {
  scroll-behavior: smooth;
}
html {
  scroll-behavior: smooth;
}
.qna-check ul {
  list-style: none;
}

.qna-check ul li:before {
  content: "\2714\0020";
}
</style>
